import { useEffect, useState } from 'react';
import Header from '../components/Header';
import SortSelect from '../components/SortSelect';
import Product from '../components/Product';
import product0Image from '../res/images/product-0.jpeg';
import product1Image from '../res/images/product-1.jpeg';
import product2Image from '../res/images/product-2.jpeg';
import product3Image from '../res/images/product-3.jpeg';
import product4Image from '../res/images/product-4.jpeg';
import product5Image from '../res/images/product-5.jpeg';
import './catalog.scss';

const PRODUCTS = [
  {
    id: 0, imgSrc: product0Image, title: ' TATRAN',
    desc: '    ,    .', price: 120000,
    minCount: 2, maxCount: 5,
  },
  {
    id: 1, imgSrc: product1Image, title: ' VILORA',
    desc: '  ,   .        .', price: 21000,
    minCount: 3, maxCount: 7,
  },
  {
    id: 2, imgSrc: product2Image, title: ' MENU',
    desc: '  -     .', price: 34000,
    minCount: 4, maxCount: 5,
  },
  {
    id: 3, imgSrc: product3Image, title: ' ASKESTA',
    desc: '        ', price: 68000,
    minCount: 2, maxCount: 7,
  },
  {
    id: 4, imgSrc: product4Image, title: ' LUNAR',
    desc: '   ,     ', price: 40000,
    minCount: 3, maxCount: 5,
  },
  {
    id: 5, imgSrc: product5Image, title: ' Nastan',
    desc: '      .', price: 80000,
    minCount: 4, maxCount: 7,
  },
];
const Catalog = () => {
  const [products, setProducts] = useState(PRODUCTS);
  const [sortBy, setSortBy] = useState('price-asc');
  useEffect(() => {
    setProducts(PRODUCTS.concat().sort(
      ({price: a}, {price: b}) => sortBy === 'price-asc' ? a - b : b - a
    ));
  }, [sortBy]);
  return <div className="catalog-page">
    <Header />
    <div className="catalog-page__content">
      <SortSelect className="catalog-page__select" value={sortBy} onChange={(event) => setSortBy(event.target.value)} />
      <div className="catalog-page__products">
        {products.map(product => <Product {...product} key={product.id} />)}
      </div>
    </div>
  </div>;
};

export default Catalog;